          margin 5px 0
          >.name
            color $color_black_text_0
            font-weight bold
            line-height 1.4em
            width: 100%
            margin-bottom 3px
          >.other-title
            line-height 1.1em
            color $color_gray_text
            font-size 1rem
            width 60px
          >.other-value
            line-height 1.1em
            color $color_gray_text
            font-size 1rem
            width calc(100% - 70px)
            word-break break-all
            white-space normal
        >.delete
          @extend .flex-center
          position absolute
          width 60px
          // height 100%
          background-color $color_theme
          color $color_white
          font-size 0.9rem
          top -11px
          right -100px
          bottom -11px
      &:last-child
        >.right
          >.delete
            bottom -10px

    // 改期乘客
    .change-passenger-infor
      @extend .flex-row
      @extend .flex-start
      width 100%
      padding 10px 0
      >.left
        @extend .flex-center
        width 60px
        height 60px
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
      >.right
        width calc(100% - 60px)
        >.item
          @extend .flex-row
          @extend .flex-between
          width 100%
          margin 5px 0
          >.name
            color $color_black_text_0
            font-weight bold
            line-height 1.4em
            width: 100%
            margin-bottom 3px
          >.other-title
            line-height 1.1em
            color $color_gray_text
            font-size 1rem
            width 60px
          >.other-value
            line-height 1.1em
            color $color_gray_text
            font-size 1rem
            width calc(100% - 70px)
            word-break break-all
            white-space normal

    // 保险
    .insure
      @extend .flex-row
      @extend .flex-between
      width 100%
      padding 10px 0
      >.details
        @extend .flex-row
        @extend .flex-start
        width calc(100% - 30px)
        >.title
          @extend .flex-row
          @extend .flex-start
          font-size 0.8rem
          color $color_black_text_0
          >.logo
            width 20px
            height 20px
            background-size 100% 100%
            margin 0 5px
        >.little-title
          font-size 0.7rem
          color $color_gray_text
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
      // >.checkbox-default
      //   width 20px
      //   height 20px
      //   border 1px solid $color_gray_text
      //   border-radius 3px
    .rules-view
      font-size 0.7rem
      line-height 1.3em
      width 100%
      padding 10px 15px
      text-align justify
      >span
        color $color_blue_text
  >.footer
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 50px
    background-color $color_white
    border-top 1px solid $color_gray_layer
    padding 0 15px
    >.price
      @extend .flex-row
      @extend .flex-start
      width 50%
      height 100%
      >p
        font-size 1rem
        color $color_theme
        font-weight bold
        margin-right 5px
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        transition all 300ms
  .detailsDialog
    position absolute !important
    width 100%
    left 0
    bottom 0
    background-color $color_white
    max-height 70%
    overflow hidden
    .detailsDialog-custom-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      height 44px
      >p
        font-size 0.8rem
        &:nth-child(1)
          color $color_black_text_0
        &:nth-child(2)
          color $color_theme
          >span
            &:nth-child(2)
              color $color_black_text_0
              margin-left 5px
    .detailsDialog-tips
      @extend .flex-row
      @extend .flex-end
      width 100%
      padding 10px 15px
      color $color_theme
      font-size 0.8rem
  .show-more
    @extend .flex-center
    position absolute !important
    width 100%
    left 0
    bottom 0
    height 15px
    color $color_black_text_0
    font-size 0.6rem
    transition all 300ms
  .rulesDialog
    width 100%
    height 100%
    overflow hidden
  .transportWholeRulesDialog
    position absolute !important
    width 100%
    left 0
    bottom 0
    background-color $color_white
    overflow hidden
    .transportWholeRulesDialog-text
      @extend .flex-row
      @extend .flex-start
      width 100%
      font-size 0.8rem
      line-height 1.2em
</style>



