<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="currentValue">
      <c-header :title="$t('base.ViolationOfTravelRank')" isBackNav></c-header>
      <hs-scroll class="table-view">
        <ul>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-header">
                <p v-if="!noValue(JSON.stringify(details.GoPassengers))">去程违反差旅职级人员:<span v-for="(item, index) in details.GoPassengers" :key="index">{{item.Name}}、</span><br/></p>
                <p v-if="!noValue(JSON.stringify(details.BackPassengers))">返程违反差旅职级人员:<span v-for="(item, index) in details.BackPassengers" :key="index">{{item.Name}}、</span><br/></p>
                <p>{{$t('base.PleaseChooseTheReason')}}:</p>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-1
              v-for="(item, index) in details.ReasonCodes" :key="index"
              :value="item.ReasonDescription"
              logoName="select.png"
              :showLogo="selectedIndex === index"
              @click.native="selectedIndex = index"></hs-cell-1>
            <hs-cell-1 v-if="details.IsDisplayOtherItem || !details.ReasonCodes || details.ReasonCodes.length === 0" logoName="select.png" :showLogo="selectedIndex === undefined" @click.native="selectedIndex = undefined">
              <div class="other-reason" :style="{width: selectedIndex === undefined ? 'calc(100% - 30px)' : '100%'}">
                <textarea v-model="otherReason" :placeholder="$t('base.OtherReasons')"></textarea>
              </div>
            </hs-cell-1>
            <hs-gradient-button :title="$t('base.NextStep')" @click="_gotoDetails"></hs-gradient-button>
          </hs-section>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
// 选择违反差旅职级原因页面
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import {mapGetters, mapMutations} from 'vuex'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  computed: {
    ...mapGetters([
      'flightOrder'
    ]),
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  data() {
    return {
      selectedIndex: 0,
      otherReason: ''
    }
  },
  watch: {
    otherReason(val) {
      !this.noValue(val) && (this.selectedIndex = undefined)
    }
  },
  methods: {
    ...mapMutations([
      'SET_FLIGHT_ORDER'
    ]),
    _gotoDetails() {
      if (this.noValue(this.otherReason.trim()) && (this.selectedIndex === undefined)) {
        this.$toast(this.$t('TM.PleaseSelectOrFillInTheReasonForViolatingTheTravelRank'))
        return
      } else if (this.otherReason.length > 50) {
        this.$toast(this.$t('TM.TheLengthOfTheViolationOfTheTravelRankCannotExceedXWords'))
        return
      }
      let res = JSON.parse(JSON.stringify(this.flightOrder))
      let ViolationCabinRankName = ''
      this.details.GoPassengers && (ViolationCabinRankName = this.details.GoPassengers.map(v => v.Name).join('、'))
      this.details.BackPassengers && (ViolationCabinRankName += this.details.BackPassengers.map(v => v.Name).join('、'))
      res.ViolationCabinRankName = ViolationCabinRankName
      res.ViolationCabinRankReason = this.selectedIndex === undefined ? `其他: ${this.otherReason}` : this.details.ReasonCodes[this.selectedIndex].ReasonDescription
      this.SET_FLIGHT_ORDER(res)
      this.currentValue = false
      this.$emit('delegate')
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsGradientButton,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  background-color $color_white
  z-index 200
  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    background-color $color_white
    .custom-cell-header
      width 100%
      padding 10px 0
      line-height 1.2em
    .other-reason
      width 100%
      padding 15px 0
      >textarea
        width 100%
        height 80px
        background-color $color_white
        font-size 1rem
        padding 10px
        border 1px solid $color_gray_layer
        border-radius 2px
        resize none
        outline none
        box-shadow 0px 0px 0px rgba(0, 0, 0, 0)
        -webkit-appearance none
</style>



